.container{
    text-align: center;
    max-width: 700px;
    margin: auto;
    /* color: white; */
}

#card{
    display: flex;
    justify-content: space-between;
    border: 1px solid black;
    border-radius: 5px;
    margin: auto auto 10px auto;
    padding: 10px;
    background-color: azure;
}

.profilepage{
    padding: 10px;
    text-align: center;
}

.clock{
    display: flex;
    justify-content: space-between;
}

button{
    height: fit-content;
    margin-top: 15px;
}

.postsContainer{
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}

.posts{
    max-width: 380px;
    max-height: 400px;
    border: 1px solid black;
    border-radius: 10px;
    margin-bottom: 20px;
    padding: 5px;
}

.userdetails{
    border: 1px solid black;
    border-radius: 10px;
    padding: 10px;
    display: flex;
    justify-content: space-between;
    text-align: left;
}

#backbtn{
    background-color: azure;
    border: 1px solid black;
    border-radius: 2px;
    padding: 3px;
}

#startPausebtn{
    background-color: rgb(15, 167, 33);
    border: 1px solid black;
    border-radius: 2px;
    padding: 3px;
}